declare const gadgets: any;
declare const finesse: any;

import { Resolver } from 'one-core';

export const loader = (
    options: any = {},
    init: (opts: any) => void,
) => {
    return new Promise((resolve, reject) => {
        const { HubSettings } = gadgets;
        HubSettings.onConnect = async () => {
            let base: string;
            // Finesse container re-writes the urls included 
            // in main gadget mark up (html) 
            const img = document.getElementById(
                'L_5CC6352F51BC44D8BE60C902B79C442C',
            ) as HTMLImageElement;

            if (img) {
                var src = img.src;
                const index = src.lastIndexOf('__loc.png');
                if (index !== -1) {
                    base = src.substr(0, index);
                }
            }

            const resolver: Resolver = (base) ?
                ((url: string) => (`${base}${url}`)) :
                ((url: string) => (url));

            const { url } = options;
            const resolved = resolver(url);

            const {
                params = {
                    cache: 'no-cache',
                    headers: {
                        "Content-Type": "application/json",
                    },
                },
            } = options;

            fetch(resolved, params).then((resp: any) => {
                if (!(resp.ok)) {
                    const { status } = resp;
                    reject({
                        message: 'Error fetching settings',
                        description: `status: ${status} url: ${resolved}`
                    });
                } else {
                    resp.json().then((settings: any) => {
                        
                        const {
                            gadget: { Config },
                            restservices: { User },
                            cslogger: { ClientLogger },
                            clientservices: { ClientServices },
                        } = finesse;

                        // initialize the client servvices
                        ClientServices.init(Config);

                        // initailize the logger
                        const { Hub } = gadgets;
                        ClientLogger.init(Hub, Config.id, Config);

                        new User({
                            id: Config.id,
                            onLoad: (user: any) => {
                                init({
                                    user,
                                    resolver,
                                    settings,
                                });
                            },
                        });

                        const {
                            containerservices: {
                                ContainerServices
                            },
                        } = finesse;

                        // initiailize the container
                        // and adjust the gadget height
                        const services = ContainerServices.init();
                        services.addHandler(ContainerServices.Topics.ACTIVE_TAB, () => {
                            const { window: gadWin } = gadgets;
                            // activate the current tab
                            if ((gadWin) && (gadWin.adjustHeight)) {
                                gadWin.adjustHeight();
                            }
                        });

                        services.makeActiveTabReq();
                        resolve({
                            settings,
                            services,
                        });
                    }).catch((exp: any) => {
                        reject({
                            message: 'Error fetching settings, invalid content',
                            description: exp.message,
                        });
                    });
                }
            }).catch((exp: any) => {
                reject({
                    message: 'Error fetching settings, server unreachable',
                    description: `server unreachable, url: ${resolved}`
                });
            });
        };
    });
}

