import { Event, EventType } from './dialog';

/**
 * Debug utility to log event data 
 * @param event 
 */
export const traceEvent = (
  event: Event,
  currCall: any = {},
) => {
  const {
    type, data: { user, dialog },
  } = event;

  const ustate = user.getState();
  if (!(dialog)) {
    console.debug(
      'gadget-voice-trace: %s',
      JSON.stringify({
        ustate,
        currCall,
      }),
    );
  } else {
    const state = dialog.getState();
    const dialogId = dialog.getId();
    const callType = dialog.getCallType();

    const parts = [];
    const participants = dialog.getParticipants();
    for (const { state: pstate, mediaAddress } of participants) {
      parts.push({ pstate, mediaAddress });
    }

    console.debug(
      'gadget-voice-trace: %s',
      JSON.stringify({
        ustate,
        state,
        dialogId,
        callType,
        parts,
        currCall,
        type: EventType[type],
      }),
    );
  }
};
