import {
    Event,
    EventType,
    actions,
    findAgent,
    traceDialog,
    bindDialogEvents,
} from './dialog';

export const bindHotKeyEvents = (
    context: any,
    handler: (event: any) => void
) => {
    const { log, user } = context;
    bindDialogEvents(user, (event: Event) => {
        const { type, data: { dialog } } = event;
        console.debug(
            'one-gadget-voice',
            'hotkeys, dialog trace', {
                type,
                dialogTrace: traceDialog(
                    user,
                    dialog,
                ),
            }
        );

        if ((type === EventType.DIALOG_CHANGE) || (type === EventType.DIALOG_LOAD)) {
            const callType = dialog.getCallType();

            if ((callType === 'PREROUTE_ACD_IN') ||
                (callType === 'CONSULT_OFFERED') ||
                (callType === 'TRANSFER')) {

                let active = false;

                const agent = findAgent(user, dialog); // current agent
                if (!((agent) && (agent.state !== 'WRAP_UP'))) {
                    log.debug(
                        'one-gadget-voice',
                        'hotkeys, ignoring dialog due to current user state', {
                            state: (agent) && (agent.state),
                        }
                    );
                } else {
                    let participants = dialog.getParticipants();

                    participants = participants.filter((participants: any) => {
                        const { state, mediaAddress } = participants;
                        const filtered = (
                            (state !== 'DROPPED') &&
                            (state !== 'WRAP_UP')
                        );
                        log.debug(
                            'one-gadget-voice',
                            'hotkeys, filtering participant', {
                                state,
                                filtered,
                                mediaAddress,
                            }
                        );
                        return filtered;
                    });

                    if ((participants) && (participants.length > 0)) {
                        active = true;
                        for (const participant of participants) {
                            const { state } = participant;
                            if (state !== 'ACTIVE') {
                                active = false;
                                break;
                            }
                        }
                    }
                }

                const mediaProps = dialog.getMediaProperties();
                const dnis = mediaProps.dialedNumber;

                // enable action on buttons
                log.debug(
                    'one-gadget-voice',
                    'hotkeys, updating actions on change', {
                        dnis,
                        active,
                    }
                );

                handler({
                    dnis,
                    active,
                    actions: actions(
                        context,
                        user,
                        dialog,
                    )
                });
            }
        } else if (type === EventType.DIALOG_DELETE) {
            log.debug(
                'one-gadget-voice',
                'hotkeys, actions cleared on delete'
            );
            handler(undefined);
        }
    });
}