import { Interact, Event } from 'one-interact';
import { VoiceEvent } from 'one-interact-voice';

import {
  Event as DialogEvent,
  EventType as DialogEventType,
  actions,
  findAgent,
  extractCallVars,
  bindDialogEvents,
} from './dialog';

import { traceEvent } from './trace';

class ActiveCall {
  private id: string;
  private type: string;

  clear() {
    this.id = undefined;
    this.type = undefined;
  }

  change(id: string, type: string) {
    let changed = false;
    if ((this.id) && (this.id === id)) {
      this.type = type;
      changed = true;
    }
    return changed;
  }

  update(id: string, type: string) {
    this.id = id;
    this.type = type;
  }

  matches(id: string, type: string) {
    return (
      (this.id) &&
      (this.type) &&
      (this.id === id) &&
      (this.type === type)
    );
  }
}

/**
 * active call
 */
const activeCall = new ActiveCall();

/**
 * Call types map
 * indexed by add/load and agent state
 */
const CallTypes: any = {
  'add': {
    'ACTIVE': [
      'CONFERENCE', 'TRANSFER',
    ],
    'ALERTING': [
      'PREROUTE_ACD_IN', 'OTHER_IN', 'TRANSFER',
    ],
  },

  'load': {
    'HELD': [
      'PREROUTE_ACD_IN', 'OTHER_IN', 'TRANSFER',
    ],
    'ALERTING': [
      'PREROUTE_ACD_IN', 'OTHER_IN', 'TRANSFER',
    ],
    'ACTIVE': [
      'PREROUTE_ACD_IN', 'OTHER_IN', 'TRANSFER',
      'CONFERENCE', 'CONSULT_OFFERED',
    ]
  }
};

/**
 * helper to check if the call type is in the list
 * associated with event (add, load) and agent state
 * @param event event (add, load)
 * @param state agent state
 * @param type call type
 */
const matchType = (
  event: string, state: string, type: string
) => {
  const matches = (
    ((CallTypes[event])[state]).indexOf(type) !== -1
  );
  return matches;
}

/**
 * Bind finesse dialog events to Interact reactions
 * @param voice 
 * @param params 
 */
export const bindEvents = (context: any, events: Interact<Event>) => {
  const { user, settings = {} } = context;
  const { media: { voice = {} } = {} } = settings;
  const { cvmap = {} } = voice;

  bindDialogEvents(user, (event: DialogEvent) => {
    traceEvent(event, activeCall);
    const { type, data: { dialog } } = event;

    if (type === DialogEventType.DIALOG_CHANGE) {
      // update active call type changes 
      activeCall.change(
        dialog.getId(),
        dialog.getCallType(),
      );
    } else if (type === DialogEventType.DIALOG_ADD) {

      const agent = findAgent(user, dialog);
      const state = ((agent) && (agent.state));
      const callType = dialog.getCallType();

      if (
        ((state === 'ALERTING') || (state === 'ACTIVE')) &&
        (matchType('add', state, callType))
      ) {
        const dialogId = dialog.getId();

        // set active
        activeCall.update(
          dialogId,
          callType,
        );

        events({
          type: VoiceEvent.ARRIVE,
          data: extractCallVars(
            dialog,
            cvmap,
          ),
          actions: actions(
            context,
            user,
            dialog,
          ),
        });
      }
    } else if (type === DialogEventType.DIALOG_DELETE) {
      const matches = activeCall.matches(
        dialog.getId(),
        dialog.getCallType(),
      );

      if (matches) {
        // clear state
        activeCall.clear();

        events({
          type: VoiceEvent.END,
          data: extractCallVars(
            dialog,
            cvmap,
          ),
          actions: actions(
            context,
            user,
            dialog,
          ),
        });
      }
    } else if (type === DialogEventType.DIALOG_LOAD) {
      const agent = findAgent(user, dialog);
      const state = ((agent) && (agent.state));
      const callType = dialog.getCallType();

      if (
        ((state === 'ALERTING') || (state === 'ACTIVE') || (state === 'HELD')) &&
        (matchType('load', state, callType))
      ) {
        const dialogId = dialog.getId();
        activeCall.update(
          dialogId,
          callType,
        );

        events({
          type: VoiceEvent.ARRIVE,
          data: {
            ...extractCallVars(dialog, cvmap),
            reload: true,
          },
          actions: actions(
            context,
            user,
            dialog,
          ),
        });
      }
    }
  });
};
