/**
 * Dialog Event Type
 */
export enum EventType {
    DIALOG_ADD = 200,
    DIALOG_CHANGE = 210,
    DIALOG_LOAD = 220,
    DIALOG_DELETE = 230,
}

/**
 * Dialog Event 
 */
export interface Event {
    type: EventType;
    data: {
        user: any,
        dialog: any,
    };
}

export const traceDialog = (
    user: any, dialog: any,
) => {
    const ustate = user.getState();
    const state = dialog.getState();
    const dialogId = dialog.getId();
    const callType = dialog.getCallType();

    const parts = [];
    const participants = dialog.getParticipants();
    for (const { state: pstate, mediaAddress } of participants) {
        parts.push({ pstate, mediaAddress });
    }

    return {
        ustate,
        state,
        dialogId,
        callType,
        parts,
    };
};

/**
 * Helper to get the participant corresponding to
 * the current agent
 * @param user 
 * @param dialog 
 */
export const findAgent = (user: any, dialog: any) => {
    let match;
    const extn = user.getExtension();
    const participants = dialog.getParticipants();
    for (const participant of participants) {
        const { mediaAddress } = participant;
        if (extn === mediaAddress) {
            match = participant;
            break;
        }
    }
    return match;
};

/**
 * Helper to extract call data from dialog
 * @param dialog 
 * @param cvmap 
 */
export const extractCallVars = (dialog: any, cvmap: any) => {
    // default values
    const mediaProps = dialog.getMediaProperties();
    const data: any = {
        'key': dialog.getId(),
        'ani': dialog.getFromAddress(),
        'dnis': mediaProps.dialedNumber,
    };

    // call variables
    if (cvmap) {
        const cvs = Object.keys(cvmap);
        if (cvs.length > 0) {
            for (const cv of cvs) {
                const key = cvmap[cv];
                data[key] = mediaProps[cv];
            }
        }
    }
    return data;
};

export const actions = (context: any, user: any, dialog: any) => {
    const { log } = context;
    return {
        transfer(to: string, from?: string): Promise<any> {
            return new Promise((resolve, reject) => {
                const mediaAddr = from || user.getExtension();
                log.debug(
                    'one-gadget-voice',
                    'hotkeys, initiating call transfer', {
                        to,
                        mediaAddr,
                    }
                );

                dialog.initiateDirectTransfer(mediaAddr, to, {
                    success: (rsp: any) => {
                        const { object } = rsp;
                        resolve(object);
                    },
                    error: (rsp: any) => {
                        const { error } = rsp;
                        if (!(error)) {
                            reject({
                                type: 'Unknown',
                                message: 'Unknown error transferring the call'
                            });
                        } else {
                            const { errorType, errorMessage } = error;
                            reject({
                                type: errorType,
                                message: errorMessage,
                            });
                        }
                    }
                });
            });
        },

        consult(to: string, from?: string): Promise<any> {
            return new Promise((resolve, reject) => {
                const mediaAddr = from || user.getExtension();
                log.debug(
                    'one-gadget-voice',
                    'hotkeys, initiating call consult', {
                        to,
                        mediaAddr,
                    }
                );
                dialog.makeConsultCall(mediaAddr, to, {
                    success: (rsp: any) => {
                        const { object } = rsp;
                        resolve(object);
                    },
                    error: (rsp: any) => {
                        const { error } = rsp;
                        if (!(error)) {
                            reject(reject({
                                type: 'Unknown',
                                message: 'Unknown error consulting the call'
                            }));
                        } else {
                            const { errorType, errorMessage } = error;
                            reject({
                                type: errorType,
                                message: errorMessage,
                            });
                        }
                    }
                });
            });
        }
    }
};

// list of event handlers
const handlers: ((event: Event) => void)[] = [];

export const bindDialogEvents = (
    user: any,
    handler: (event: Event) => void,
) => {
    handlers.push(handler);

    // register dialog call back only once
    if (handlers.length === 1) {
        user.getDialogs({
            onCollectionAdd: (dialog: any) => {
                handlers.forEach(h => h({
                    type: EventType.DIALOG_ADD,
                    data: {
                        user, dialog,
                    },
                }));

                dialog.addHandler('change', (dialog: any) => {
                    handlers.forEach(h => h({
                        type: EventType.DIALOG_CHANGE,
                        data: {
                            user, dialog,
                        },
                    }));
                });
            },

            onCollectionDelete: (dialog: any) => {
                handlers.forEach(h => h({
                    type: EventType.DIALOG_DELETE,
                    data: {
                        user, dialog,
                    },
                }));
            },

            onLoad: (dialogs: any) => {
                const collection = dialogs.getCollection();
                const ids = Object.keys(collection);

                for (let id of ids) {
                    const dialog = collection[id];
                    dialog.addHandler('change', (dialog: any) => {
                        handlers.forEach(h => h({
                            type: EventType.DIALOG_CHANGE,
                            data: {
                                user, dialog,
                            },
                        }));
                    });

                    handlers.forEach(h => h({
                        type: EventType.DIALOG_LOAD,
                        data: {
                            user, dialog,
                        },
                    }));
                }
            }
        });
    }
}
